.dialogs {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: start;

  &__users {
    display: flex;
    justify-content: flex-start;
    width: 200px;
    background: aliceblue;
  }

  &__messages {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: calc(100% - 225px);
    background: azure;
  }
}
.formBlock{
  display: flex;
}
.button {
  font-family: inherit;
  font-size: 11px;
  background: royalblue;
  color: white;
  padding: 0.7em 1em;
  display: flex;
  align-items: center;
  border: none;
  border-radius: 16px;
  overflow: hidden;
  transition: all 0.2s;
}

.button span {
  display: block;
  margin-left: 0.3em;
  transition: all 0.3s ease-in-out;
}

.button svg {
  display: block;
  transform-origin: center center;
  transition: transform 0.3s ease-in-out;
}
.button:hover .svg-wrapper {
  animation: fly-1 0.6s ease-in-out infinite alternate;
}

.button:hover svg {
  transform: translateX(1.2em) rotate(45deg) scale(1.1);
}

.button:hover span {
  transform: translateX(5em);
}

.button:active {
  transform: scale(0.95);
}

@keyframes fly-1 {
  from {
    transform: translateY(0.1em);
  }

  to {
    transform: translateY(-0.1em);
  }
}
